:root {
    --primary: #00308F;
}

.btn {
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor: pointer;
    white-space: nowrap;
}

.btn--primary {
    background-color: var(--primary);
    color: #232323;
    border: 1px solid var(--primary); 
}

.btn--outline {
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border: 1px solid var(--primary); 
    transition: all 0.3s ease-out;
}
 
.video {
    margin-top: 10px;
    border-radius: 10px;
    width: 10rem;
    align-self: center;
    z-index: 4;
}
.video:hover {
    box-shadow: 3px 10px 10px #000;
    transition: all 0.5 ease-in;
}

.btn--medium {
    padding: 8px 20px;
    font-size: 18px;
}

.btn--large {
    padding: 12px 26px;
    font-size: 20px;
}

.btn--mobile {
    text-align: center;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 24px;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;

}

.btn--wide {
    padding: 12px 64px;
    font-size:20px;
}

.btn--large:hover, .btn--medium:hover, .btn--mobile:hover {
    transition: all 0.3s ease-out;
    background: #242526;
    color: #fff;
}

.btn--wide:hover {
    color: #fff;
    background-color: #00308F;
    color: #242424;
    transition: all 0.2s ease-out;
}


.blue {
    background-color: #00308F;
    color: #fff;
    border: none;
}

.red {
    background-color: #f00946;
    color: #fff;
    border: none;
}

.primary {
    background-color: #242424;
    color: #fff;
    border: none;
}
.primary:hover {
    background-color: #fff;
    color: #242424;
    border: none;
}

.green {
    background-color: #25ce4a;
    color: #fff;
    border: none;
}

.green:hover {
    background-color: #242424;
}

.btn-link {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    padding: 8px 16px;
    height: 100%;
    width: 100%;

}

@media screen and (max-width: 960px) {
    .video {
        width: 10rem;
    }
}




